/*  box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

/*    */
ul,
ol {
  padding: 0;
}

/*    */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption {
  margin: 0;
}

/*    -  body */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
}

/*      ul  il,     class*/
ul,
ol {
  list-style: none;
}

/*  a,    ,     */
a {
  text-decoration-skip-ink: auto;
  text-decoration: none;
  color: currentColor;
}

button {
  padding: 0px;
  border: none;
}

/*     */
img {
  max-width: 100%;
  display: block;
}

/*       */
input,
button,
textarea,
select {
  font: inherit;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
  -webkit-appearance: none;
  margin: 0;
}
